import React, { useEffect, useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Sidebar from './Sidebar';

const WindowResize = (props) => {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    function captureWindowResizeEvent() {
      const currentWidth = window.innerWidth;
      setWindowWidth(currentWidth);
    }
    window.addEventListener("resize", captureWindowResizeEvent);
    return () => {
      window.removeEventListener("resize", captureWindowResizeEvent);
    };
  }, []);
  return (
    <React.Fragment>
      <Device windowWidth={windowWidth} DeviceMetaData={props.DeviceMetaData} />
    </React.Fragment>
  );
};

const Device = (props) => {
  const device = props.DeviceMetaData.filter(
    (device) =>
      device.deviceMaxWidth >= props.windowWidth &&
      device.deviceMinWidth <= props.windowWidth
  );
  return (
    <Container>
      <Row className="justify-content-center">
        <Col className="mt-4">
          <div
            className="card bg-light border-warning"
            style={{
              borderRadius: "25px",
              boxShadow: "0px 5px 8px grey",
            }}
          >
            <div
              className="card-header bg-dark text-white"
              style={{
                borderTopLeftRadius: "25px",
                borderTopRightRadius: "25px",
              }}
            >
              <h4>Window Size</h4>
              <h1 className="text-warning">{props.windowWidth}</h1>
            </div>
            <div className="card-body">
              <Row className="justify-content-center">
                <Col className="col-12">
                  <img
                    className="img-fluid"
                    src={require(`../Assets/${device[0].deviceImageURL}`)}
                    alt={device[0].deviceType + " Device"}
                  />
                </Col>
                <Col className="pt-3 col-12">
                  <h3>{device[0].deviceType}</h3>
                </Col>
              </Row>
            </div>
          </div>
        </Col>
        {device[0].deviceMinWidth > 1025 ? <Sidebar /> : null}
      </Row>
    </Container>
  );
};

export default WindowResize;
